@keyframes loading {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

@keyframes shake {
    10%,
    90% {
        transform: translate3d(-4px, 0, 0);
    }
    20%,
    80% {
        transform: translate3d(6px, 0, 0);
    }
    30%,
    50%,
    70% {
        transform: translate3d(-10px, 0, 0);
    }
    40%,
    60% {
        transform: translate3d(10px, 0, 0);
    }
}
